@import "../../utilities/_variables.scss";

.houses-chart__container {
    background: $bgColorPrimary;
    width: 100vw;
    height: fit-content;
    padding: 8em 0 4.125em;

    @media screen and (max-width: $mediaTablet) {
        padding: 3em 2em;
    }

    @media screen and (max-width: $mediaMobile) {
        padding: 1.375em 1em;
    }

    .icon-house-icon:before { 
        content: '\e80a'; 
        color: #D24246;
        font-size: 4rem;
        @include flex-center;
        margin-bottom: 0.5em;

        @media screen and (max-width: $mediaMobile) {
            display: none;
        }
    }

    span {
        @include flex-center;
        font-size: 4rem;
        color: #D24246;
        margin-bottom: 0.5em;

        @media screen and (max-width: $mediaMobile) {
            display: none;
        }
    }

    h2 {
        margin: 0 1em;

        @media screen and (max-width: $mediaMobile) {
            font-size: 1.75rem;
            line-height: 2.25rem;
        }
    }

    .p-large {
        margin: 0.875em auto 1.90625em;
        max-width: 742px;
        text-align: center;
    }

    .chart {
        @include chart;
    }
}